import React, {useEffect, useState} from 'react';
import {Timeline} from "./pages/Timeline";
import styled, {createGlobalStyle} from "styled-components";
import {BrowserRouter, Route, useLocation, useParams} from "react-router-dom";
import {Leaderboard} from "./pages/Leaderboard";
import {PageSelector} from "./components/PageSelector/PageSelector";
import {faHistory, faTrophy} from "@fortawesome/free-solid-svg-icons";
import {Header} from "./components/Header/Header";
import {useLazyWorker, useWorker} from "./hook/useWorker";
import {ProgressScan} from "./components/Header/ProgressScan";
import 'react-tippy/dist/tippy.css'

const GlobalStyle = createGlobalStyle`
    body {
      min-height: 100vh;
      background-color: #111;
      background-image: url('/wallpaper/wallpaper-1.jpg');
      background-repeat: no-repeat;
      background-position: center;
      background-attachment: fixed;
      background-size: cover;
    }
 `;

const Container = styled.div`
  padding-top: 40px;
  background: rgba(0, 0, 0, .5);
  backdrop-filter: blur(30px);
`

const Center = styled.div`
  display: flex;
  justify-content: center;
  flex: 1;
`

const RouterContent = () => {

    const {type, id} = useParams();
    const location = useLocation();

    const [refresh, setRefresh] = useState(false);

    const PAGES = [
        {
            icon: faTrophy,
            title: 'Leaderboard',
            path: `/${type}/${id}`
        },
        {
            icon: faHistory,
            title: 'Timeline',
            path: `/${type}/${id}/timeline`
        }
    ]

    const selected = location.pathname;

    const [characters] = useWorker('characters', {type, id});

    const [displayName] = useWorker('profile', {type, id}, {initial: '<loading>'});

    const [activities, , loadActivities] = useLazyWorker('activities', {
        type,
        id,
        refresh,
        characters: characters?.characters?.map(it => it.id) || []
    });

    useEffect(() => {
        characters && loadActivities();
    }, [refresh, location, characters]);

    return <React.Fragment>

        <Header name={displayName}/>

        <Container>

            {characters && <ProgressScan characters={characters.characters} id={id}/>}

            {
                activities && <React.Fragment>

                    <Center>
                        <PageSelector
                            onRefresh={() => setRefresh(true)}
                            pages={PAGES}
                            selected={selected}/>
                    </Center>

                    <div>
                        <Route path="/:type/:id/timeline" exact>
                            <Timeline items={activities.items || []}/>
                        </Route>

                        <Route path="/:type/:id" exact>
                            <Leaderboard flawless={activities.flawless} items={activities.items || []} totals={activities.totals}/>
                        </Route>
                    </div>
                </React.Fragment>

            }

        </Container>

    </React.Fragment>;

}

function App() {
    return <React.Fragment>
        <GlobalStyle/>
        <BrowserRouter>
            <Route path="/:type/:id">
                <RouterContent/>
            </Route>
            <Route path="/" exact>
                <Header name='Raid + Dungeon Timeline'/>
            </Route>
        </BrowserRouter>
    </React.Fragment>
}

export default App;
