import raidIcon from "../../pages/icons/raid.png";
import dungeonIcon from "../../pages/icons/dungeon.png";
import {VerticalTimelineElement} from "react-vertical-timeline-component";
import Moment from "react-moment";
import React, {useMemo, useState} from "react";
import {Activity, ActivityCard, ActivityIcon, ActivityIconParent, Period} from "./TimelineElementStyle";
import ReactCardFlip from 'react-card-flip';
import {TimelineElementFront} from "./TimelineFront";
import {TimelineElementBack} from "./TimelineBack";
import {ActivityHeader} from "../ActivityHeader/ActivityHeader";

export const TimelineElement = ({
                                    id,
                                    period,
                                    display,
                                    players,
                                    image,
                                    tags,
                                    trolled,
                                    type,
                                    index
                                }) => {

    const icon = <ActivityIconParent>
        <ActivityIcon alt="icon" src={type === 4 ? raidIcon : dungeonIcon}/>
    </ActivityIconParent>;

    const date = useMemo(() => <Period index={index}>
        <strong><Moment fromNow>{period}</Moment></strong>
        <small><Moment format="dddd, MMMM Do YYYY, HH:MM">{period}</Moment></small>
    </Period>, [period, index]);

    const [flipped, setFlipped] = useState(false);

    const act = <Activity
        onClick={() => !flipped && setFlipped(true)}
        onMouseLeave={() => {
            setFlipped(false);
        }}
        position={index % 2 ? 'right' : 'left'}
        image={image}>

        <ActivityHeader
            id={id}
            display={display}
            flipped={flipped}
            setFlipped={setFlipped}/>

        <ActivityCard>
            <ReactCardFlip infinite={true} isFlipped={flipped} flipDirection="vertical">
                <TimelineElementFront tags={tags.map(it => ({
                    tag: it,
                    activity: {display, type}
                }))}/>
                <TimelineElementBack players={players} trolled={trolled}/>
            </ReactCardFlip>
        </ActivityCard>

    </Activity>

    return <VerticalTimelineElement
        date={date}
        contentStyle={{padding: 0, background: 'none', boxShadow: 'none'}}
        iconStyle={{boxShadow: 'none'}}
        icon={icon}>
        {act}
    </VerticalTimelineElement>

}
