import React from "react";
import {Tag, Tags} from "./TimelineElementStyle";
import {Tooltip} from "react-tippy";
import {raidReport, tooltipFor} from "../../util/util";

export const TimelineElementFront = ({tags}) => {
    return <div>
        <Tags>
            {tags.map(it =>
                <Tooltip
                    key={it.tag}
                    arrow={true}
                    style={{marginRight: 16}}
                    title={tooltipFor(it.tag, it.activity)}
                    position="top">
                    <Tag onClick={() => raidReport(it.activity)}>
                        {it.tag.replace('-', ' ')}
                    </Tag>
                </Tooltip>
            )}
        </Tags>
    </div>
}
