import React, {useState} from "react";
import {TimelineElement} from "../components/TimelineElement/TimelineElement";
import {VerticalTimeline} from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';
import styled from "styled-components";
import {Selector} from "../components/Selector/Selector";
import intersection from "lodash.intersection";
import {faSortAmountDownAlt, faSortAmountUpAlt} from "@fortawesome/free-solid-svg-icons";

const TimelinePage = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  
  & .vertical-timeline::before {
    margin-top: 3px;
    width: 1px;
    background: black;
    outline: 0.5px solid rgba(255, 255, 255, .7);
    outline-offset: 1px;
  }
  
  & .vertical-timeline-element-date {
    padding: 0;
  }
`

const Empty = styled.p`
  color: white;
  opacity: .3;
  text-align: center;
  font-size: 30pt;
  flex: 1;
`

const Filter = styled.div`
 display: flex;
 flex-direction: column;
 padding: 0 64px;
 margin-top: 4px;
 & > div {
  margin-bottom: 16px;
 }
`

const TimelineContainer = styled.div`
  display: flex;
  width: 100%;
`;

const defaultTypes = [
    'raid',
    'dungeon'
];

const defaultMode = [
    'solo',
    'solo-flawless',
    '2-man',
    '3-man',
    'flawless',
    'first-completion',
    'day-one',
    'day-two',
    'week-one'
];

const isInFilter = (types, modes, activity) => {
    const type = activity.type === 4 ? 'raid' : 'dungeon';
    const isTypeValid = types.includes(type);
    const isTagsValid = !modes.length || modes.length === defaultMode.length || intersection(activity.tags, modes).length > 0;
    return isTypeValid && isTagsValid;
}

export const Timeline = ({ items }) => {

    const [types, setTypes] = useState(defaultTypes);
    const [modes, setModes] = useState(defaultMode);
    const [directions, setDirections] = useState(['down']);

    const records = Array.from(items).filter(it => isInFilter(types, modes, it));

    if (directions[0] === 'up') {
        records.reverse();
    }

    const elements = records.map((it, idx) => <TimelineElement key={it.id} {...it} index={idx}/>);

    const timeline = <VerticalTimeline>
        {elements}
    </VerticalTimeline>;

    return <TimelinePage>
        <TimelineContainer>
            <Filter>
                <Selector
                    items={['up', 'down']}
                    selected={directions}
                    exclusive={true}
                    icons={{up: faSortAmountDownAlt, down: faSortAmountUpAlt}}
                    onSelect={setDirections}/>
                <Selector
                    vertical
                    items={defaultTypes}
                    selected={types}
                    onSelect={setTypes}/>
                <Selector
                    vertical
                    items={defaultMode}
                    selected={modes}
                    onSelect={setModes}/>
            </Filter>
            {records.length ? timeline : <Empty>No records found for current filter.</Empty>}
        </TimelineContainer>
    </TimelinePage>

}
