import styled from "styled-components";
import React from "react";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {useHistory} from "react-router-dom";
import {Tooltip} from "react-tippy";
import {faSync} from "@fortawesome/free-solid-svg-icons";

const PageItem = styled.div`
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid ${p => p.selected ? 'white' : 'rgba(255, 255, 255, .4)'};
  color: ${p => p.selected ? 'white' : 'rgba(255, 255, 255, .5)'};
  cursor: pointer;
  padding: 2px;
  margin: 16px;
  background-clip: content-box;
  &:hover {
    background-color: rgba(255, 255, 255, .1);
  }
`;

const SelectorContainer = styled.div`
  display: flex;
  max-width: 50%;
  padding-bottom: 16px;
  align-items: center;
  flex: 1;
  &:before,
  &:after{
    flex: 1;
    height: 1px;
    background: rgba(255, 255, 255, .5);
    margin: 0 16px;
    content: '';
  }
`;

export const PageSelector = ({pages, onRefresh, selected}) => {

    const history = useHistory();

    const items = pages.map(it => <PageItem
        key={it.path}
        selected={selected === it.path}
        onClick={() => history.push(it.path)}>
        <Tooltip
            arrow={true}
            title={it.title}
            position="bottom">
            <FontAwesomeIcon icon={it.icon}/>
        </Tooltip>
    </PageItem>);

    return <SelectorContainer>
        {items}
        <PageItem
            key={'refresh'}
            onClick={onRefresh}>
            <Tooltip
                title="Force refresh data"
                arrow={true}
                position="bottom">
                <FontAwesomeIcon icon={faSync}/>
            </Tooltip>
        </PageItem>
    </SelectorContainer>
}
