export const Transform = {

    tags: (tags) => tags.map(it => it.replace('-', ' ')),

}

export const raidReport = (activity) => {
    if (!activity.id) return;
    const act = activity.type === 4 ? 'raid' : 'dungeon';
    window.open(`https://${act}.report/pgcr/` + activity?.id);
}

const BOSSES = {
    'Leviathan': 'Calus',
    'Scourge of the Past': 'the Insurrection Prime',
    'Garden of Salvation': 'the Sanctified Mind',
    'Crow of Salvation': 'Gahlran',
    'Leviathan, Eater of Worlds': 'Argos',
    'The Shattered Throne': 'DÃ»l Incaru',
    'Prophecy': 'Kell Echo',
    'Zero Hour': 'Siviks',
    'Pit of Heresy': 'Zulmak',
    'Whisper': 'the bosses'
}

export const tooltipFor = (tag, activity) => {

    const act = activity.type === 4 ? 'raid' : 'dungeon';
    const name = activity.display.short;
    const fresh = activity.fresh;

    switch (tag) {

        case 'first-completion':
            return 'First time this activity was completed';

        case 'solo-flawless':
            return 'Completed the dungeon without die in a fireteam of one';

        case 'solo':

            if (act === 'dungeon' && fresh) {
                return 'Completed the dungeon in a fireteam of one';
            }

            if (name === 'Last Wish') {
                return 'Completed the Queen Walk in a fireteam of one';
            }

            return `Defeated ${BOSSES[activity.display.short]} in a fireteam of one`;

        case 'flawless':
            return `Completed the ${act} without any fireteam's deaths`;

        case 'day-one':
            return `Completed the ${act} in the first day`;

        case 'day-two':
            return `Completed the ${act} in the second day`;

        case 'week-one':
            return `Completed the ${act} in the first week`;

        case '2-man':
        case '3-man':
            const players = tag === '2-man' ? 'two' : 'three';

            if (name === 'Last Wish') {
                return 'Completed the Queen Walk in a fireteam of ' + players;
            }

            return `Defeated ${BOSSES[activity.display.short]} in a fireteam of ${players}`;

        default:
            return '';
    }


}
