import React from "react";
import {ActivityContent, Player, PlayerBlock, PlayerInfo, Trolled} from "./TimelineElementStyle";

export const TimelineElementBack = ({players, trolled}) => {

    const playersItems = players.map(player =>
        <Player key={player.name + player.class} completed={player.completed}>
            <PlayerBlock>
                <img src={player.icon} alt="icon"/>
                <PlayerInfo>
                    <span onClick={() => window.open(`/${player.type}/${player.id}`)}>{player.name}</span>
                    <small>{player.class}</small>
                </PlayerInfo>
            </PlayerBlock>
            <div>
                {player.deaths}
            </div>
        </Player>
    )

    return <div>
        <ActivityContent>
            <div>
                {playersItems}
            </div>
            {
                trolled > 0.7 &&
                players.some(it => it.deaths) &&
                <Trolled>
                    <b>TROLLED</b><br/>
                    before flawless mode was activated
                </Trolled>
            }
        </ActivityContent>
    </div>
}
