import styled from "styled-components";
import React, {useCallback} from "react";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";

const Item = styled.div`
  cursor: pointer;
  border: 1px solid rgba(255, 255, 255, .3);
  text-align: center;
  padding: 16px 24px;
  border-right-width: 0;
  flex: 1;
  background: ${p => p.selected ? 'rgba(255, 255, 255, .1)' : 'none'};
  &:hover {
    outline: 1px solid white;
    outline-offset: -4px;
  } 
`;

const SelectorContainer = styled.div`
  display: inline-flex;
  flex-direction: ${p => p.vertical ? 'column' : 'row'};
  outline: 1px solid rgba(255, 255, 255, .3);
  text-transform: uppercase;
  color: white;
  padding: 8px;
  & ${Item} {
    border-${p => p.vertical ? 'bottom' : 'right'}-width: 0;
    &:last-of-type {
      border-${p => p.vertical ? 'bottom' : 'right'}-width: 1px;
    }  
  }
} 
`;

export const Selector = ({items, horizontal = true, vertical = false, selected, onSelect, exclusive = false, icons = {}}) => {

    const onSelectItem = useCallback((item) => {

        if (exclusive) {
            return onSelect([item]);
        }

        if (selected.includes(item)) {
            return onSelect(selected.filter(it => it !== item));
        }

        onSelect([...selected, item]);

    }, [selected, onSelect, exclusive])

    const selectors = items.map(it =>
        <Item key={it}
              onClick={() => onSelectItem(it)}
              selected={selected.includes(it)}>
            {icons[it] ? <FontAwesomeIcon icon={icons[it]}/> : it.replace('-', ' ')}
        </Item>
    )

    return <SelectorContainer horizontal={horizontal} vertical={vertical}>
        {selectors}
    </SelectorContainer>
}
