import React from "react";
import styled from "styled-components";

const Slider = styled.div`
    position:relative;
    width: 100%;
    z-index: 10;
    height: ${p => p.visible ? '2px' : 0};
    overflow-x: hidden;
    
    .line{
        position:absolute;
        opacity: 0.3;
        background:#fff;
        width:150%;
        height: 2px;
    }

    .sub{
        position:absolute;
        background:#fff;
        height: 2px;
    }
    .inc{
        animation: increase 2s infinite;
    }
    .dec{
        animation: decrease 2s 0.5s infinite;
    }
    
    @keyframes increase {
        from { left: -5%; width: 5%; }
        to { left: 130%; width: 100%;}
    }
    @keyframes decrease {
        from { left: -80%; width: 80%; }
        to { left: 110%; width: 10%;}
    }
`

export const ProgressBar = ({ visible }) => <Slider visible={visible}>
    <div className="line"/>
    <div className="sub inc"/>
    <div className="sub dec"/>
</Slider>;
