import React, {useState} from "react";
import styled from "styled-components";
import {Col, Container, Row} from 'react-grid-system';
import {useWorker} from "../../hook/useWorker";
import hunterClass from "./icons/class_hunter.svg";
import warlockClass from "./icons/class_warlock.svg";
import titanClass from "./icons/class_titan.svg";

const CLASS_ICONS = {
    Titan: titanClass,
    Warlock: warlockClass,
    Hunter: hunterClass
}

const ScanCharactersContainer = styled.div`
 display: flex;
 justify-content: center;
 padding: 16px 0;
`;

const Character = styled.div`
  border: 1px solid rgba(255, 255, 255, .3);
  outline: 1px solid rgba(255, 255, 255, .5);
  outline-offset: 2px;
  position: relative;
  margin: 8px 0;
  overflow: hidden;
  animation: ${p => p.step > 0 && p.step < 6 ? 'rotate 1s linear forwards infinite' : ''};
  opacity: ${p => !p.step ? .3 : 1};

  & img {
    width: 100%;
     display: block;
  }
  
  & > div {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    top: 0;
    left: 0;
    justify-content: center;
    position:absolute;
    background-color: rgba(0,0,0,.3);
    color: white;
    & span {
      font-size: 20pt;
    }
    & img {
      width: 36px;
      margin-left: 16px;
    }
 }
   
  &:after {
    z-index: 999;
    content: '';
    background: rgba(255, 255, 255, .1);
    height: 100%;
    position:absolute;
    top: 0;
    right: 0;
    width: ${p => (6 - (p.step || 0)) / 6 * 100}%;
    transition: all .3s;
    backdrop-filter: grayscale(100%);
  }
   
   @keyframes rotate {
      100% {
        border-color: rgba(255, 255, 255, .1);
      }
      75% {
        border-color: rgba(255, 255, 255, .4);
      }
      50% {
        border-color: rgba(255, 255, 255, .7);
      } 
      25% {
        border-color: rgba(255, 255, 255, .4);
      }
      0% {
        border-color: rgba(255, 255, 255, .1);
      }

  }
  
`;

const LoaderDescription = styled.div`
  color: white;
  text-blink: 'hello';
  text-align: center;
  animation: blink 1s ease-in-out infinite;
  
  @keyframes blink {
    0% { opacity: 1; }
    50% { opacity: 1; }
    50.01% { opacity: 0; }
    100% { opacity: 0; }
  }
`;

export const ProgressScan = ({characters = [], id}) => {

    const [completion, setCompletion] = useState({});
    const [lastMode, setLastMode] = useState('');

    useWorker('progress', null, {
        onData: ({completed, character, mode, id: user}) => {
            if (id === user) {
                completion[character] = (completion[character] || 0) + 1;
                setCompletion({...completion});
                setLastMode(mode === 4 ? 'raid' : 'dungeon');
            }
        }
    });

    const items = characters.map(it => <Col xs={12} md={4} key={it.id}>
            <Character step={completion[it.id]}>
                <img src={it.image} alt="cover"/>
                <div>
                    <span>{it.light}</span>
                    <img src={CLASS_ICONS[it.class]} alt="class"/>
                </div>
            </Character>
        { completion[it.id] && completion[it.id] < 6 && <LoaderDescription>analyzing <b>{lastMode}</b> reports</LoaderDescription> }
        </Col>
    );

    return <ScanCharactersContainer>
        <Container>
            <Row>
                {items}
            </Row>
        </Container>
    </ScanCharactersContainer>
        ;

}
