import React from "react";
import 'react-vertical-timeline-component/style.min.css';
import styled from "styled-components";
import groupBy from "lodash.groupby";
import sortBy from "lodash.sortby";
import {Col, Container, Row} from 'react-grid-system';
import {LeaderboardPage} from "../components/LeaderboardPage/LeaderboardPage";
import * as moment from 'moment';

const LeaderboardContainer = styled.div`
  text-align: center;
`;

const SectionHeader = styled.div`
  color: white;
  font-weight: bold;
  font-size: 30pt;
  display: flex;
  align-items: center;
  flex: 1;
  margin-bottom: 24px;
  
  &:before,
  &:after{
    flex: 1;
    height: 1px;
    background: rgba(255, 255, 255, .5);
    margin: 0 16px;
    content: '';
  }
    
  &:before {
    max-width: 20%;  
  }
`;

export const LeaderboardSection = ({totals, section, items, flawless}) => {

    const records = Object.entries(groupBy(items, it => it.display.name.split(/[:(]/)[0].trim()));

    const pages =
        sortBy(records, ([, value]) => -moment(value[0].display.release))
            .map(([title, items]) => <Col key={title} lg={6} xs={12}>
                <LeaderboardPage flawless={flawless} totals={totals} items={items} title={title}/>
            </Col>);

    return <LeaderboardContainer>

        <SectionHeader>{section}</SectionHeader>

        <Container>
            <Row gutterWidth={24}>
                {pages}
            </Row>
        </Container>

    </LeaderboardContainer>

}

export const Leaderboard = ({items, flawless, totals}) => {

    const records = groupBy(items, it => it.type === 4 ? 'RAID' : 'DUNGEON');

    const sections = Object.keys(records).sort().reverse().map((it) =>
        <LeaderboardSection
            key={it}
            flawless={flawless}
            totals={totals}
            items={records[it]}
            section={it}/>
    );

    return <div>
        {sections}
    </div>

}
