import swivel from "swivel";
import {useCallback, useEffect, useRef, useState} from "react";

export const useWorker = (event, payload, options) => {

    const {initial, onData, skip} = options || {};
    const prevSkip = useRef();

    const [data, setData] = useState(initial);
    const [loading, setLoading] = useState(false);

    const listener = useCallback((ctx, data) => {
        setData(data);
        onData && onData(data);
        setLoading(false);
    }, []);

    useEffect(() => {
        if (skip || (skip && skip === prevSkip.current)) {
            return;
        }
        prevSkip.current = skip;
        swivel.on('res/' + event, listener);
        setLoading(true);
        swivel.emit('req/' + event, payload);
        return () => swivel.off('res/' + event, listener);
    }, [skip]);

    return [data, loading];

}

export const useLazyWorker = (event, payload, initial = {}) => {

    const [data, setData] = useState(initial);
    const [loading, setLoading] = useState(false);

    const listener = useCallback((ctx, data) => {
        setData(data);
        setLoading(false);
    }, []);

    useEffect(() => {
        swivel.on('res/' + event, listener);
        return () => swivel.off('res/' + event, listener);
    }, [event, payload, listener]);

    const execute = useCallback(() => {
        setLoading(true);
        swivel.emit('req/' + event, payload);
    }, [event, payload]);

    return [data, loading, execute];

}
