import React, {useEffect, useState} from "react";
import styled from "styled-components";
import {useIsTyping} from "use-is-typing";
import useInput from 'react-use-input';
import {ProgressBar} from "../ProgressBar/ProgressBar";
import {useLazyWorker} from "../../hook/useWorker";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faTimes} from "@fortawesome/free-solid-svg-icons";
import {Col, Container, Row} from 'react-grid-system';
import useDocumentTitle from '@rehooks/document-title';

const HeaderContainer = styled.div`
  position: relative;
  top: 0;
  width: 100%;
  transition: all .3s;
  padding: 4px 0;
  backdrop-filter: blur(32px) brightness(80%);
  box-shadow: 0 0 1px 2px rgba(0, 0, 0, .4);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  z-index: 999;
  
  & > div:first-of-type {
    color: white;
    flex: 1;
    font-weight: bold;
    font-size: 30pt;
    text-align: center;
    text-shadow: 0 0 1px 1px rgba(0, 0, 0, .2);
  }
  
  & > div:last-of-type {
    flex: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-right: 24px;
  }
  
  & * {
    transition: all .3s;
  }
  
  & input {
    border: none;
    outline: none;
    color: white;
    padding: 8px;
    font-size: 12pt;
    color: rgba(255, 255, 255, .7);
    color: white;
    width: 100%;
    
    &::placeholder {
        color: white;
        opacity: .6;
    }

    &::selection {
      background: rgba(0, 0, 0, .5);
      color: white;
    }
  }
  
`;

const SearchBlock = styled.div`
  width: 100%;
  max-width: 400px;
  position: relative;
  
  & input,
  & > div:last-of-type {
      background: ${p => p.visible ? '#181818' : 'rgba(0, 0, 0, .2)'};
      backdrop-filter: blur(16px);
  }
        
  & > div:first-of-type {
    margin: 8px 0;
    display: flex;
  } 
`;

const SearchResults = styled.div`
  width: 100%;
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  align-items: stretch;
  max-height: 400px;
  overflow-y: auto;
  height: ${p => p.visible ? 'auto' : 0};
`;

const Result = styled(Col)`
  display: flex;
  align-items: center;
  padding: 8px;
  color: white;
  cursor: pointer;
  min-width: 150px;
  border-bottom: 1px solid rgba(255, 255, 255, .1);
  
  &:hover {
    background: rgba(255,255,255,.1);
  }
 
  &:last-of-type {
     border-bottom: none;
  }
 
  img {
    max-width: 16px;
    margin-right: 8px;
    background: rgba(0, 0, 0, .3);
  }
`;

const ClearResults = styled.span`
  color: rgba(255,255,255,.7);
  display: flex;
  padding: 8px 16px;
  align-items: center;
  cursor:pointer;
`;

export const Header = ({name}) => {

    const [query, setQuery] = useInput();

    const [isTyping, register] = useIsTyping();
    const [resultsVisible, setResultsVisible] = useState(false);
    const [results, loading, search] = useLazyWorker('search', {query}, []);

    useDocumentTitle(name + ' | Raid + Dungeon');

    useEffect(() => {
        if (!isTyping && query) {
            search();
            setResultsVisible(true);
        }
    }, [isTyping, query]);

    const items = results.map(({id, type, name, icon}) =>
        <Result
            xs={12}
            md={6}
            key={id}
            onClick={() => window.location.href = `/${type}/${id}`}>
                <img alt='icon' src={icon}/> {name}
        </Result>)
    return <div>

        <HeaderContainer>
            <div>{name}</div>
            <div>
                <SearchBlock>
                    <div>
                        <input
                            ref={register}
                            value={query}
                            spellCheck={false}
                            placeholder="Search your username or SteamID"
                            onChange={setQuery}/>
                        <ClearResults onClick={() => {
                            setQuery({target: {value: ''}});
                            setResultsVisible(false);
                        }}>
                            <FontAwesomeIcon icon={faTimes}/>
                        </ClearResults>
                    </div>
                </SearchBlock>
                <SearchResults visible={resultsVisible}>
                    <ProgressBar visible={loading}/>
                    <Container>
                        <Row>{items}</Row>
                    </Container>
                </SearchResults>

            </div>
        </HeaderContainer>


    </div>;

}
