import React from "react";
import styled from "styled-components";
import {raidReport, tooltipFor} from "../../util/util";
import {ActivityHeader, Header} from "../ActivityHeader/ActivityHeader";
import uniqBy from "lodash.uniqby";
import {Tag} from "../TimelineElement/TimelineElementStyle";
import {Tooltip} from "react-tippy";

const Page = styled.div`
    border: 1px solid rgba(255, 255, 255, .3);
    outline: 1px solid white;
    outline-offset: 2px;
    background-image: url(${p => p.image});
    background-size: cover;
    min-height: 300px;
    overflow: hidden;
    margin-bottom: 24px;
    display: flex;
    flex-direction: column;
`;

const ItemsSlider = styled.div`
 padding: 32px 32px 25px;
 display: flex;
 align-items: flex-end;
 flex: 1;
`;

const PageHeader = styled.div`
 & ${Header} {
  width: calc(100% - 48px);
 }
`;

const needMode = (display) => {
    return display.name.startsWith('Leviathan') ||
        display.name.startsWith('The Whisper') ||
        display.name.startsWith('Zero Hour');
}

const LeaderboardTags = ({items, flawless = {}}) => {


    const {display, type} = items[0];

    const all_tags = items.flatMap(it => it.tags.filter(it => !it.startsWith('first'))
        .map(tag => ({
                tag,
                mode: it.display.mode,
                activity: it
            })
        )
    );

    if (!items.includes('flawless') && flawless[display.short]) {
        all_tags.push({tag: 'flawless', activity: {display, type}});
    }

    let unique = uniqBy(all_tags, it => it.tag);

    if (unique.find(it => it.tag === 'day-one')) {
        unique = unique.filter(({tag}) => tag !== 'day-two' && tag !== 'week-one');
    }

    if (unique.find(it => it.tag === 'day-two')) {
        unique = unique.filter(({tag}) => tag !== 'week-one');
    }

    const showModes = needMode(display);

    const tags = unique.map(it =>
        <Tooltip
            key={it.tag}
            arrow={true}
            style={{marginRight: 16}}
            title={tooltipFor(it.tag, it.activity)}
            position="top">
            <Tag onClick={() => raidReport(it.activity)}>
                {it.tag.replace('-', ' ')}
                {showModes && <small>{it.mode}</small>}
            </Tag>
        </Tooltip>
    );

    return <ItemsSlider>
        {tags}
    </ItemsSlider>

}

export const LeaderboardPage = ({title, items, totals, flawless}) => {

    const {display, image} = items[0];

    const header = {
        name: title,
        short: display.short,
        description: display.description
    };

    return <Page image={image}>
        <PageHeader>
            <ActivityHeader display={header} totals={totals}/>
        </PageHeader>
        <LeaderboardTags items={items} flawless={flawless}/>
    </Page>;

}
